@mixin user_select_none {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}
@mixin auth_title_style {
  margin: 0px 0px 15px 0px;

  font-size: 26px;
  font-weight: 600;
}
@mixin table_body_styles {
  .row {
    cursor: pointer;
  }

  .image {
    width: 50px;
    height: 50px;
    margin-left: 5px;
  }

  .tableCellText {
    overflow: hidden;
    display: -webkit-box;

    max-height: 100px;

    font-size: 13px;
    text-overflow: ellipsis;
    word-break: break-word;

    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;

    &::first-letter {
      text-transform: none;
    }
  }
}
